[mol_grapher_liner] {
	stroke: #C57995;
	stroke-width: 3;
	fill: none;
}

[mol_grapher_liner]:nth-of-type(2) {
	stroke: #7570C1;
}

[mol_grapher_liner_roper]:hover {
	stroke: darkblue;
}

[mol_grapher_liner_knoter] {
	fill: #FF8EBC;
}

[mol_grapher_liner]:nth-of-type(2) [mol_grapher_liner_knoter] {
	fill: #9494F5;
}

[mol_grapher_liner_knoter]:hover {
	stroke: darkblue;
}
