[mol_chart] {
	display: flex;
	flex-direction: column;
	flex: 1 1 50vmin;
}

[mol_app_chart_charter_titler] {
	display: flex;
	justify-content: center;
	font-weight: bold;
	padding: .5em;
}

[mol_chart_plotter] {
	flex: 1 1 auto;
	height: 25vmin;
}

[mol_chart_legender] {
	padding: .25em;
}

[mol_chart_legender_info] {
	display: flex;
}

[mol_chart_legender_info] > * {
	margin: .25em;
}

[mol_chart_legender_info_sampler] {
	background: #C57995;
	flex: 0 0 1em;
}

[mol_chart_legender_info]:nth-of-type(2) [mol_chart_legender_info_sampler] {
	background: #7570C1;
}

[mol_chart_legender_info_titler] {
	flex: 1 1 auto;
}
